  }

  .signin-signup{
    display: flex;
    justify-content: space-evenly;
  }
.home-wrapper {
  background-image: url(/static/media/desert-background.80236f29.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100vw;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 2fr;
  justify-items: center;
  align-items: center;
}

.header-home {
  grid-column-start: 1;
  grid-column-end: -1;
