  grid-column-start: 3;
  grid-row-start: 1;
}

.center-grid-home-box {
  grid-column-start: 2;

  grid-row-start: 2;
}

.center-grid-home-content {
  grid-column-start: 2;

  grid-row-start: 2;
  width: 30%;
  height: 20%;

  background-color: beige;
  border: 2px solid;
  display: flex;
  align-items: center;
  justify-content: center;
}

.center-grid-home-content:hover,
.center-grid-home-content:focus {
  transform: translateY(-0.25em);
}

